html,
body {
	height: 100%;
	font-family: "微软雅黑";
}

* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

a {
	color: #000;
	text-decoration: none;
}

a:hover,
a:focus {
	color: #2a6496;
}

a:focus {
	outline: thin dotted;
	outline: 5px auto -webkit-focus-ring-color;
	outline-offset: -2px;
}

.padd_40 {
	padding-top: 80px;
	background: #F5F5F5;
	overflow-x: hidden;
}

.a {
	text-align: center;
	line-height: 40px;
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	z-index: 10;
	border-bottom: 1px #ccc solid;
	background: #f50;
	color: #fff;
}

.tab {
	display: flex;
	line-height: 40px;
	position: fixed;
	top: 40px;
	width: 100%;
	z-index: 10;
	border-bottom: 3px #f1f1f1 solid;
}

.tab a {
	width: 33.333333%;
	background: #fff;
	text-align: center;
	font-size: 0.32rem;
}

.tab .active {
	border-bottom: 1px #26c400 solid;
	color: #26c400;
}

.panel {
	margin: 0;
}

.refreshtip {
	position: absolute;
	left: 0;
	width: 100%;
	margin: 10px 0;
	text-align: center;
	color: #999;
}

.loadtip {
	display: block;
	width: 100%;
	line-height: 40px;
	height: 40px;
	text-align: center;
	color: #999;
	border-top: 1px solid #ddd;
}

/*.swiper-container, .w{height: calc(100vh - 120px);}*/

.swiper-slide {
	height: auto;
}

.text-center {
	text-align: center;
}

.list-group {
	padding-left: 0;
	margin-bottom: 20px;
}

.list-group-item {
	position: relative;
	display: block;
	padding: 10px 15px;
	background-color: #fff;
	border-bottom: 1px solid #ddd;
}

.list-group-item:first-child {
	border-top-left-radius: 4px;
	border-top-right-radius: 4px;
}

.my-order-iphone1 {
	width: auto;
	line-height: 1.3rem;
	margin-right: 3%;
}

.commodity-name {
	line-height: 0.8rem;
	width:100%;
	margin: auto;
	padding: 2%;
	background-color: #f4f4f4;
}

.pay-button-myorder {
	text-align: right;
	overflow: hidden;
	position: relative;
}

.bg26c400{background-color: #26c400;}
.bgf58d05{background-color: #f58d05;}
.bg99{background-color: #999;}
.order-details-header{position: relative;}
.pay-button-myorder button{	
	border: none;
	text-align: center;
	color: #fff;
	font-size: 0.32rem;
	padding: 0.2rem 0.2rem;
	border-radius: 5px;
	margin-top: 2%;
	margin-bottom: 2%;
	margin-right: 2%;
}
.commodity-name li{line-height: .5rem;padding: .2rem 0;font-size: .3rem;}
.toggleright{margin-right:10%;}
.toggleButton{    
	position: absolute;
    right: 2%;
    top:48%;
    }
.toggleButton img{width: 80%;}
.commodity-name.f032.toggleCon {
    display: none;
}


